import { Component, OnInit, OnDestroy } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { InternalNgModuleRef } from '@angular/core/src/linker/ng_module_factory';
import { CurrencyPipe } from '@angular/common/src/pipes';
import { NumberValueAccessor } from '@angular/forms/src/directives/number_value_accessor';
import { susieBio, debBio, jamesBio } from '../../../assets/textStore'

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})
export class TeamComponent implements OnInit {

  currentText: string = "";

  constructor() { }

  ngOnInit() {
    document.getElementById("bio").style.height = "80px";
    this.currentText = "Select a team member to learn more"
  }

  ngOnDestroy() {
    this.closeBio();
  }

  closeBio(): void{
    document.getElementById("bio").style.height = "0px";
    this.currentText = "";
  }

  switchBioTrigger(currentBio: number){
    document.getElementById("bio").style.height = "0px";
    if (document.getElementById("bio").clientHeight != 0){
      setTimeout(() => this.switchBio(currentBio), 500);
    } else {
      this.switchBio(currentBio);
    }
  }

  switchBio(currentBio: number){
    if(currentBio == 0){
      document.getElementById("bio").style.height = "300px";
      this.currentText = "";
    }
    if(currentBio == 1){
      document.getElementById("bio").style.height = "300px";
      this.currentText = susieBio;
    }
    if(currentBio == 2){
      document.getElementById("bio").style.height = "300px";
      this.currentText = debBio;
    }
    if(currentBio == 3){
      document.getElementById("bio").style.height = "300px";
      this.currentText = jamesBio;
    }
  }  
}




